import React, { useState } from 'react'
import './toDo.sass'
// import ToDoItems from '../toDoItems';

const ToDo = () =>{
    const [count, setCount] = useState(0);
    const [todoItems, setTodoItems] = useState([]);
    const [values, setValues] = useState('')



    const onSubmit = (e) =>{
        e.preventDefault()
        setCount(count + 1)
        if(values === ''){
            setCount(count)
        }else{
            setTodoItems(prevItems => ([...prevItems, { values, id: prevItems.length + 1}]))
            setValues('')
        }
        
        e.target.reset()
    }

    const element = todoItems.map(({id, values: itemValue})=>(
        <li 
        className='toDoItem'
        key={id}>
            {itemValue}
            <div><button key={id} type='button' 
        className='btn-trash'
        onClick={()=>{
            setCount(count -1)
            setTodoItems((data)=> data.filter(elem => elem.id !== id))
        }}
        ><i className='fa fa-trash-o'></i>
        </button></div>
        </li>
        ))
    return(
        <div className='toDo'>
            <div className='count'>
                {count}
            </div>
            <form
            onSubmit={onSubmit}
            className='formStyle'
            >
                <input 
                type='text' 
                placeholder='введите Вашу заметку'
                onChange={(e)=>{
                    setValues(e.target.value)
                }}
                />
                <button 
                type='submit'
                >Добавить заметку</button>
            </form>
            {
                <ul>
                    { element }
                </ul>
            }
        </div>
    )
}

export default ToDo